px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n#hamburger{\n  display: none;\n  cursor: pointer;\n}\n.sidebar{\n  display: none;\n}\n#likeButtonContainer{\n  padding: 0;\n  border: none;\n  width: 0px;\n}\n@media screen and (max-width: 320px) {\n  .navbar {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n  }\n  .form-container {\n    width: auto;\n  }\n\n  .navbar ul {\n    width: 100vw;\n    display: flex;\n    background-color: $nav-footer-color;\n    position: absolute;\n    flex-direction: column;\n    justify-content: center;\n    right: 0;\n    transform: translateX(100%);\n    transition: all 1.2s ease-in-out;\n    z-index: -1;\n    opacity: 0;\n  }\n  .navbar ul.slide{\n    opacity: 1;\n    transform: translateX(0);\n  }\n  #hamburger:hover{\n    transform: rotate(360deg);\n    color: #f39c12;\n  }\n  .card-container {\n    display: grid;\n    grid-template-columns: 1fr;\n  }\n}\n@media screen and (max-width: 649px) {\n  .navbar {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n  }\n  #hamburger {\n    display: flex;\n  }\n  .navbar ul {\n    width: 100vw;\n    display: flex;\n    background-color: $nav-footer-color;\n    position: absolute;\n    flex-direction: column;\n    justify-content: center;\n    right: 0;\n    transform: translateX(100%);\n    transition: all 1.2s ease-in-out;\n    z-index: -1;\n    opacity: 0;\n  }\n  .navbar ul.slide{\n    opacity: 1;\n    transform: translateX(0);\n  }\n  #hamburger:hover{\n    transform: rotate(360deg);\n    color: #f39c12;\n    transition: all 1s;\n  }\n  .card-container {\n    display: grid;\n    grid-template-columns: 1fr;\n  }\n  .form-container {\n    width: auto;\n  }\n}\n@media screen and (min-width: 650px) {\n  .navbar {\n    display: grid;\n    grid-template-columns: 1fr auto;\n  }\n  .card-container {\n    display: grid;\n    grid-template-columns: repeat(2, 